import React from 'react'

import { NavLink } from "react-router-dom";

import cross from "./img/cross.png";

export default function menu({isActive, onClose}) {
    return (
        <div 
        style={{width:"246px",
        height:"314px",
        position:"fixed",
        backgroundColor:"white",
        marginLeft:`${isActive ? "0px" : "-246px"}`,
        transition:"500ms",
        paddingLeft:"15px",
        paddingTop:"30px",
        zIndex:"1"}} 
        className="side-menu">
            <div onClick={()=>{onClose()}} style={{marginTop:"11px",marginBottom:"20px"}}><img src={cross} alt="close menu"/></div>
            <ul className="menu-list">
                <li><NavLink to="/DDD/catalog">Каталог</NavLink></li>
                <li><NavLink to="/DDD/catalog">Головные уборы</NavLink></li>
                <li><NavLink to="/DDD/catalog">Пальто</NavLink></li>
                <li><NavLink to="/DDD/catalog">Костюмы</NavLink></li>
                <li><NavLink to="/DDD/catalog">Брюки</NavLink></li>
                <li><NavLink to="/DDD/catalog">Обувь</NavLink></li>
                <li><NavLink to="/DDD/catalog">Аксессуары</NavLink></li>
            </ul>
        </div>
    )
}
